import React, { useState, useCallback } from "react";
import styled from "styled-components";
import Gears from "assets/images/gears.png";
import BoxLogo from "assets/images/boxlogo.png";
import YunaCoin from "assets/images/yunaCoin.png";
import { Heading, CardBody, Button } from "@pancakeswap-libs/uikit";
import { Link } from "react-router-dom";
import { useWeb3React } from "@web3-react/core";
import useI18n from "hooks/useI18n";
// import { useAllHarvest } from "hooks/useHarvest";
// import useFarmsWithBalance from "hooks/useFarmsWithBalance";
import ConnectWalletButton from "components/ConnectWalletButton";
import Card from "components/custom/Card";
import CakeHarvestBalance from "./CakeHarvestBalance";
import CakeWalletBalance from "./CakeWalletBalance";

const StyledFarmStakingCard = styled(Card)`
  background-image: url(${BoxLogo});
  background-repeat: no-repeat;
  background-size: 30%;
  background-position: 80% 50%;
  min-height: 376px;
`;

const Block = styled.div`
  margin-bottom: 16px;
`;

const CardImage = styled.img`
  margin-bottom: 16px;
  margin-left: 10px;
  transform: scaleX(-1);
`;

const Label = styled.div`
  color: ${({ theme }) => theme.colors.textSubtle};
  font-size: 14px;
`;

const Actions = styled.div`
  margin-top: 24px;
`;

const FarmedStakingCard = () => {
  const [pendingTx, setPendingTx] = useState(false);
  const { account } = useWeb3React();
  const TranslateString = useI18n();
  // const farmsWithBalance = useFarmsWithBalance();
  // const balancesWithValue = farmsWithBalance.filter(
  //   (balanceType) => balanceType.balance.toNumber() > 0,
  // );

  // const { onReward } = useAllHarvest(
  //   balancesWithValue.map((farmWithBalance) => farmWithBalance.pid),
  // );

  // const harvestAllFarms = useCallback(async () => {
  //   setPendingTx(true);
  //   try {
  //     await onReward();
  //   } catch (error) {
  //     // TODO: find a way to handle when the user rejects transaction or it fails
  //   } finally {
  //     setPendingTx(false);
  //   }
  // }, [onReward]);

  return (
    <StyledFarmStakingCard>
      <CardBody>
        <Heading size="xl" mb="24px">
          {TranslateString(542, "Your Rewards")}
        </Heading>
        <CardImage src={YunaCoin} alt="logo" width={84} height={84} />
        <Block>
          <Label>{TranslateString(544, "YUNA to Harvest")}:</Label>
          <CakeHarvestBalance />
        </Block>
        <Block>
          <Label>{TranslateString(546, "YUNA in Wallet")}:</Label>
          <CakeWalletBalance />
        </Block>
        <Actions>
          {account ? (
            <Link to="/farms">
              <Button
                id="harvest-all"
                // disabled={balancesWithValue.length <= 0 || pendingTx}
                // onClick={harvestAllFarms}
                width="100%"
              >
                {pendingTx
                  ? TranslateString(548, "Collecting YUNA")
                  : TranslateString(
                      532,
                      `Harvest all (4)`,
                    )}
                {/* {pendingTx
                  ? TranslateString(548, "Collecting YUNA")
                  : TranslateString(
                      532,
                      `Harvest all (${balancesWithValue.length})`,
                    )} */}
              </Button>
            </Link>
          ) : (
            <ConnectWalletButton width="100%" />
          )}
        </Actions>
      </CardBody>
    </StyledFarmStakingCard>
  );
};

export default FarmedStakingCard;
